// Gnrateur d'exercices avec progression scaffolde
export const generateUnit3Exercises = (): Exercise[] => {
  const exercises: Exercise[] = []

  // =============================================================================
  // TAPE 1 : RECONNAISSANCE NOMBRES DE BASE (0-5)
  // =============================================================================

  // 1.1 Reconnaissance audio nombres
  exercises.push({
    id: 'audio_numbers_basic',
    type: 'audio_recognition',
    vocabularyItem: unit3Vocabulary[1], // een
    question: 'Quel nombre entendez-vous ?',
    options: ['un', 'deux', 'trois'].sort(() => Math.random() - 0.5),
    correctAnswer: 'un',
    context: 'Reconnaissance auditive des nombres de base'
  })

  // 1.2 Traduction nombres essentiels
  exercises.push({
    id: 'translation_zwou',
    type: 'translation',
    vocabularyItem: unit3Vocabulary[2],
    question: 'Comment dit-on "deux" en luxembourgeois ?',
    options: ['zwou', 'dri', 'vier'].sort(() => Math.random() - 0.5),
    correctAnswer: 'zwou',
    context: 'Vocabulaire numrique de base'
  })

  // =============================================================================
  // TAPE 2 : ASSEMBLAGE GUID (nombres + contexte)
  // =============================================================================

  // 2.1 Construction ge simple
  exercises.push({
    id: 'progressive_age_simple',
    type: 'progressive_building',
    vocabularyItem: unit3Vocabulary[5], // joer
    question: 'Assemblez pour dire "trois ans"',
    wordBank: ['dri', 'Joer'],
    correctAnswer: 'dri Joer',
    expectedSentence: 'dri Joer',
    hint: 'nombre + unit de temps',
    context: 'Expression de l\'ge en luxembourgeois'
  })

  // 2.2 Reconnaissance motif tlphone
  exercises.push({
    id: 'pattern_phone',
    type: 'pattern_recognition',
    vocabularyItem: unit3Vocabulary[6], // telefon
    question: 'Comment prsente-t-on un numro de tlphone ?',
    options: ['min Telefon ass', 'ech Telefon sinn', 'min sinn Telefon'].sort(() => Math.random() - 0.5),
    correctAnswer: 'min Telefon ass',
    context: 'Structure pour donner son numro de tlphone'
  })

  // =============================================================================
  // TAPE 3 : CONSTRUCTION PHRASES COURTES (ge, numro)
  // =============================================================================

  // 3.1 Phrase complte ge avec rvision
  exercises.push({
    id: 'progressive_age_complete',
    type: 'progressive_building',
    vocabularyItem: unit3Vocabulary[8], // sinn (rvision)
    question: 'Compltez : "ech _____ zwou Joer"',
    wordBank: ['sinn', 'kommen', 'schaffen'],
    correctAnswer: 'sinn',
    expectedSentence: 'ech sinn zwou Joer',
    hint: 'Verbe tre + ge (rvision Unit1)',
    context: 'Expression personnelle de l\'ge'
  })

  // 3.2 Compltion numro tlphone
  exercises.push({
    id: 'completion_phone_number',
    type: 'phrase_completion',
    vocabularyItem: unit3Vocabulary[6],
    question: 'Compltez : "min _____ ass 621"',
    options: ['Telefon', 'Joer', 'Numm'].sort(() => Math.random() - 0.5),
    correctAnswer: 'Telefon',
    expectedSentence: 'min Telefon ass 621',
    context: 'Dbut de numro de tlphone luxembourgeois typique'
  })

  // =============================================================================
  // TAPE 4 : CONSTRUCTION PHRASES COMPLTES (4-6 mots)
  // =============================================================================

  // 4.1 Prsentation complte avec ge
  exercises.push({
    id: 'sentence_age_presentation',
    type: 'sentence_construction',
    vocabularyItem: unit3Vocabulary[1],
    question: 'Prsentez-vous avec l\'ge de 4 ans',
    wordBank: ['ech', 'sinn', 'vier', 'Joer'].sort(() => Math.random() - 0.5),
    correctAnswer: 'ech sinn vier Joer',
    expectedSentence: 'ech sinn vier Joer',
    hint: 'pronom + verbe + nombre + unit',
    context: 'Prsentation personnelle avec ge'
  })

  // 4.2 Remise en ordre numro complet
  exercises.push({
    id: 'word_order_phone_complete',
    type: 'word_ordering',
    vocabularyItem: unit3Vocabulary[6],
    question: 'Ordonnez pour donner votre numro de tlphone',
    wordBank: ['min', 'Telefon', 'ass', 'vier', 'null', 'een'],
    correctAnswer: 'min Telefon ass vier null een',
    expectedSentence: 'min Telefon ass vier null een',
    hint: 'possessif + objet + verbe + chiffres',
    context: 'Communication du numro de tlphone'
  })

  // =============================================================================
  // TAPE 5 : APPLICATION CONTEXTUELLE LUXEMBOURGEOISE
  // =============================================================================

  // 5.1 Dialogue inscription crche Luxembourg
  exercises.push({
    id: 'dialogue_creche_age',
    type: 'dialogue_completion',
    vocabularyItem: unit3Vocabulary[5],
    question: 'Inscription crche Kirchberg. ge de votre enfant de 2 ans :',
    options: [
      'min Kand ass zwou Joer',
      'min Kand sinn zwou Joer',
      'min Kand kommen zwou Joer'
    ].sort(() => Math.random() - 0.5),
    correctAnswer: 'min Kand ass zwou Joer',
    context: 'Services de garde d\'enfants luxembourgeois'
  })

  // 5.2 Reconnaissance vocale numro tlphone
  exercises.push({
    id: 'speech_phone_number',
    type: 'speech_recognition',
    vocabularyItem: unit3Vocabulary[6],
    question: 'Donnez votre numro : "min Telefon ass..."',
    correctAnswer: 'min Telefon ass',
    expectedSentence: 'min Telefon ass',
    hint: 'Prononcez : myn te-le-FON ass',
    context: 'Communication de coordonnes personnelles'
  })

  // 5.3 Application pratique - rendez-vous mdical
  exercises.push({
    id: 'creative_medical_appointment',
    type: 'dialogue_completion',
    vocabularyItem: unit3Vocabulary[1],
    question: 'Rendez-vous pdiatre. ge de l\'enfant (1 an) :',
    options: [
      'een Joer',
      'een sinn',
      'een Telefon'
    ].sort(() => Math.random() - 0.5),
    correctAnswer: 'een Joer',
    context: 'Systme de sant luxembourgeois'
  })

  // 5.4 Exercice bonus - Code postal Luxembourg
  exercises.push({
    id: 'bonus_postal_code',
    type: 'pattern_recognition',
    vocabularyItem: unit3Vocabulary[4], // vier
    question: 'Quel chiffre commence tous les codes postaux luxembourgeois ?',
    options: ['vier', 'dri', 'zwou'].sort(() => Math.random() - 0.5),
    correctAnswer: 'vier',
    context: 'Connaissance pratique systme postal luxembourgeois'
  })

  return exercises
}

// Fonctions utilitaires pour nombres
function getRandomNumberDistractor(correct: string): string {
  const numbers = ['een', 'zwou', 'dri', 'vier', 'fnnef', 'sechs']
  const filtered = numbers.filter(item => item !== correct)
  return filtered[Math.floor(Math.random() * filtered.length)]
}

function generateRandomLuxembourgishPhone(): string {
  // Format tlphone luxembourgeois : 6xx xxx xxx
  const numbers = ['null', 'een', 'zwou', 'dri', 'vier']
  return `6${numbers[Math.floor(Math.random() * numbers.length)]}${numbers[Math.floor(Math.random() * numbers.length)]}`
}

// Validation spcifique Unit3
export function validateUnit3Progression(): {
  isValid: boolean
  issues: string[]
  numericalProgression: 'poor' | 'good' | 'excellent'
  practicalApplication: number // pourcentage d'exercices pratiques
} {
  const exercises = generateUnit3Exercises()
  const issues: string[] = []

  // Vrifier applications pratiques (ge, tlphone)
  const practicalCount = exercises.filter(ex =>
    ex.context?.includes('tlphone') ||
    ex.context?.includes('ge') ||
    ex.context?.includes('crche') ||
    ex.context?.includes('mdical')
  ).length

  const practicalPercentage = (practicalCount / exercises.length) * 100

  if (practicalPercentage < 40) {
    issues.push('Pas assez d\'applications pratiques des nombres (minimum 40%)')
  }

  // Vrifier progression numrique cohrente
  const numberExercises = exercises.filter(ex =>
    ex.vocabularyItem?.id?.includes('null') ||
    ex.vocabularyItem?.id?.includes('een') ||
    ex.vocabularyItem?.id?.includes('zwou') ||
    ex.vocabularyItem?.id?.includes('dri') ||
    ex.vocabularyItem?.id?.includes('vier')
  ).length

  if (numberExercises < 6) {
    issues.push('Progression numrique insuffisante pour base solide')
  }

  const numericalProgression = issues.length === 0 ? 'excellent' :
                              issues.length <= 1 ? 'good' : 'poor'

  return {
    isValid: issues.length === 0,
    issues,
    numericalProgression,
    practicalApplication: Math.round(practicalPercentage)
  }
}

